.bookCardContainer {
  min-width: 300px;
  max-width: 365px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: var(--shadow-md);
  transition: box-shadow ease-in-out 0.2s;
  display: flex;
  flex-direction: column;
  gap: 15px;
  background-color: #fff;
  flex: 1 ;
  &:hover {
    box-shadow: var(--shadow-lg);
  }
  .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h2 {
      text-transform: uppercase;
    }
    a {
      color: var(--primary-color);
      cursor: pointer;
      &:hover {
        color: var(--primary-hover);
      }
    }
  }

  .author {
    display: flex;
    gap: 8px;
    align-items: center;
    text-transform: capitalize;
    svg {
      width: 16px;
      height: 16px;
    }
  }

  .summary {
    display: flex;
    justify-content: space-between;
    gap: 8px;
    svg {
      width: 16px;
      height: 16px;
    }
    p {
      width: 100%;
      display: -webkit-box;
      -webkit-line-clamp: 6;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }

  .bookCardFooter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid var(--gray-200);
    padding: 20px 0;
    margin-top: auto;

    p {
      color: var(--gray-500);
      font-size: 14px;
    }
    .buttons {
      display: flex;
      gap: 10px;
      a{
        color: var(--primary-color);
        &:hover{
          color: var(--primary-hover);
        }
      }
    }
  }
}
