.headerContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
  margin: auto;
  .logo {
    display: flex;
    gap: 10px;
    align-items: center;
    svg {
      width: 32px;
      height: 32px;
      color: var(--primary-color);
    }
    h1 {
      font-weight: bold;
      color: var(--gray-900);
    }
  }
  a {
   
    display: flex;
    align-items: center;
    gap: 10px;
    background-color: var(--primary-color);
    color: #fff;
    padding: 10px;
    border-radius: 8px;cursor: pointer;
    transition:  ease-in-out .2s;
    svg {
      color: #fff;
      height: 20px;
      width: 20px;
    }
    &:hover{background-color: var(--primary-hover);}
  }
}
