.bookFormContainer {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin: auto;
    gap: 50px;
    padding: 50px 0;
    h2 {
      font-size: 35px;
      text-align: center;
    }
    form {
      background-color: #fff;
      box-shadow: var(--shadow-lg);
      padding: 20px;
      border-radius: 8px;
      display: flex;
      flex-direction: column;
      gap: 10px;
      input,
      textarea {
        margin-bottom: 10px;
        border: none;
        border-bottom: 1px solid var(--gray-300);
        padding: 10px;
        border-radius: 8px 8px 0 0;
        &:focus {
          border-radius: 8px;
          outline: 1px solid var(--primary-color);
        }
      }
      textarea{
          font-family: roboto, serif;
          resize: none;
      }
      .buttons{
          display: flex;gap: 20px;justify-content: end;
          a{
              display: flex;
              align-items: center;
              gap: 10px;
              background-color: var(--primary-color);
              color: #fff;
              padding: 7px 18px;
              border-radius: 8px;cursor: pointer;
              transition:  ease-in-out .2s;
          &.cancel{
              background-color: #fff;
              color: #000;
              border: 1px solid var(--gray-300);
              svg{
                  color: #000;
                  height: 16px;
                  width: 16px;
  
              }
              &:hover{
                  background-color: var(--gray-100);
              }
          }
          &.save{
              border: none;
              svg {
                  color: #fff;
                  height: 16px;
                  width: 16px;
                }
                &:hover{background-color: var(--primary-hover);      
            }
    
          }
      }}
    }
  }