.bookDetailsContainer {
  min-width: 300px;
  max-width: 800px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: var(--shadow-lg);
  transition: box-shadow ease-in-out 0.2s;
  display: flex;
  flex-direction: column;
  gap: 15px;
  background-color: #fff;
  flex: 1;
  h2 {
    text-align: center;
  }
  .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h2 {
      text-transform: uppercase;
    }
  }

  .author {
    display: flex;
    gap: 8px;
    align-items: center;
    text-transform: capitalize;
    svg {
      width: 16px;
      height: 16px;
    }
  }

  .summary,
  .review {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 8px;
    svg {
      width: 16px;
      height: 16px;
    }
    p {
      width: 100%;
      text-align: justify;
    }
    h4 {
      display: flex;
      gap: 10px;
      align-items: center;
    }
  }

  .bookCardFooter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid var(--gray-200);
    padding: 20px 0;
    p {
      color: var(--gray-500);
      font-size: 14px;
    }
    a {
      display: flex;
      align-items: center;
      gap: 10px;
      color: #000;
      padding: 7px 18px;
      border-radius: 8px;
      cursor: pointer;
      transition: ease-in-out 0.2s;
      &.back {
        background-color: #fff;
        color: #000;
        border: 1px solid var(--gray-300);
        svg {
          color: #000;
          height: 16px;
          width: 16px;
        }
        &:hover {
          background-color: var(--gray-100);
        }
      }
    }
  }
}
